@import '/src/globalStyle/variables.scss';
@import '/src/globalStyle/mixins.scss';
*,
*::before,
*::after {
    box-sizing: border-box;
}
.container {
    position: relative;
    display: inline-block;
    margin-left: 32px;
    box-shadow: 0 4px 24px 0 rgba(27, 27, 27, 0.08);

    &:before {
      content: '';
      right: 0;
      top: 0;
      width: 48px;
      height: 48px;
      background: url('../../../assets/images/icons/Arrows_down.svg') no-repeat;
      background-position-x: center;
      background-position-y: center;
      border-left: $accent-color 1px solid;
      position: absolute;
      pointer-events: none;
      display: block;
      z-index: 1;

      @media screen and (max-width: 767px) {
        border: none;
      }
    }

    .select {
      position: relative;
      width: 200px;
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;
      background: $white-color;
      border: none;
      outline: none;
      padding-left: 32px;
      margin: 0;
      @include Body;
      -webkit-border-radius: 8px;
      -moz-border-radius: 8px;
      border-radius: 8px;
      cursor: pointer;
      height: 48px;

      @media screen and (max-width: 767px) {
        width: 48px;
        font-size: 0;
        padding-left: 14px;

        &:before {
          border: none;
        }
      }
    }
  }
