@use '../../../globalStyle/variables' as v;
@use '../../../globalStyle/mixins' as m;

.advantages {
  display: flex;
  flex-direction: column;
  align-items: center;
  @include m.SectionContainer;
  max-width: 1280px;
  padding: 48px 0;
  margin: 0 auto;
  color: v.$text-green-color;

  &__container_title {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 16px;
  }

  &__container_card {
    max-width: 1110px;
    gap: 32px;
    //display: flex;
    //justify-content: center;
    //flex-wrap: wrap;
    display: grid;
    grid-template-columns: 1fr 1fr;

    @media screen and (max-width: 767px) {
      grid-template-columns: 1fr;
    }
  }

  &__card {
    min-height: 143px;
    padding: 20px 24px;
    display: flex;
    gap: 16px;
    box-shadow: 0px 4px 24px 0px rgba(0, 0, 0, 0.08);
  }

  &__card_title {
    @include m.Header2;
  }

  &__card_text {
    @include m.Link_medium;
  }

  &__title {
    @include m.Header;
    color: v.$black1B-color;
    text-align: center;
    margin-bottom: 24px;
  }
}
